@import url(https://fonts.googleapis.com/css?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Luckiest+Guy&display=swap);
body {
  background: -webkit-gradient(linear, right top, left top, from(rgb(7, 27, 82, 1)), to(rgba(0, 128, 128, 1)));
  background: linear-gradient(to left, rgb(7, 27, 82, 1) 0%, rgba(0, 128, 128, 1) 100%);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Luckiest Guy', cursive, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.card-container {
     display: grid;
     grid-gap: 5px;
     margin: 5px;
     grid-template-columns: repeat(auto-fill, minmax(200px, 300px));
     justify-content: center;
     padding:  0 auto;
     background: -webkit-gradient(linear,right top, left top,from(rgb(21, 233, 180)),to(rgb(10, 170, 245)));
     background: linear-gradient(to left,rgb(21, 233, 180),rgb(10, 170, 245));
     border-radius: 10px;    
     -webkit-transition: all 0.75s;    
     transition: all 0.75s;
     box-shadow: 1px 1px 5px rgb(30, 31, 31);   
      
}

.card-name {
color:rgb(100, 220, 250);
}
.card-container:hover {
     box-shadow: 1px 1px 15px rgb(165, 199, 191);
     cursor: pointer;
     -webkit-transform: scale(0.95);
             transform: scale(0.95);
     background: rgb(10, 170, 245);
}
.card-list {
     display: grid;
     grid-template-columns: repeat(auto-fill,minmax(200px, 300px));
     grid-gap: 20px;
     justify-content: center;
     font-size: calc(11px + 1.2vmin);
     text-shadow: 1px 1px 10px rgb(3, 59, 45);  
}


@-webkit-keyframes loading {
  from {color: rgb(21, 233, 180);}
  to {color:rgb(10, 170, 245)}
}

@keyframes loading {
  from {color: rgb(21, 233, 180);}
  to {color:rgb(10, 170, 245)}
}

.loading {
  color: rgb(100, 220, 250);
  text-shadow: 2px 2px 15px rgb(2, 34, 26);
  margin-top: 10%;
  text-align: center;
  font-size: calc(15px + 3vmin);
  font-family: 'Luckiest Guy', cursive;
  font-weight: 100;
  -webkit-animation: loading 3s linear 2s infinite alternate;
          animation: loading 3s linear 2s infinite alternate;
}

.App {
  text-align: center;
  font-family: 'Luckiest Guy', cursive;
  color: rgb(100, 220, 250);
  text-shadow: 2px 2px 15px rgb(2, 34, 26);
  -webkit-animation: loading 4s linear 2s infinite alternate;
          animation: loading 4s linear 2s infinite alternate;
}
ul {
background: -webkit-gradient(linear,right top, left top,from(rgba(21, 233, 180, .2)),to(rgba(241, 245, 247, 0.3)));
background: linear-gradient(to left,rgba(21, 233, 180, .2),rgba(241, 245, 247, 0.3));
box-shadow: 2px 2px 10px rgb(21, 233, 180);
list-style: none;
display: flex;
flex-wrap: wrap;
align-items: center;
justify-content: center;
padding: 2%;
margin: 0.5%;
border-radius: 5px;
}

.App-logo {
  height: 6.5vmin;
  top: 20%;
  left: 20%;
}
.react-logo{
  margin: 5px;
 
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
}

.App-link {
  color: #61dafb;
}


.btn-rmv{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row-reverse;
  align-items: center;
}
.btn-list {
  
  text-shadow: 2px 2px 15px rgb(2, 51, 39);
  margin: 0% auto 2% auto;
  box-shadow: 1px 1px 5px rgb(21, 233, 180);
  font-weight: 100;
}
html {
  color: inherit;
}
